import { isEmpty } from '../helper';

function CartTableAddress({ address }) {
  return (
    <table>
      <thead>
        <tr>
          <th>Endereo</th>
          <th>Nmero</th>
          <th>Complemento</th>
          <th>Bairro</th>
          <th>Cidade</th>
          <th>Estado</th>
          <th>Pas</th>
          <th>CEP</th>
        </tr>
      </thead>
      <tbody>
        {!isEmpty(address) ? (
          <tr>
            <td>{address.street}</td>
            <td>{address.number}</td>
            <td>{address.complement}</td>
            <td>{address.district}</td>
            <td>{address.city}</td>
            <td>{address.state}</td>
            <td>{address.country}</td>
            <td>{address.zipcode}</td>
          </tr>
        ) : (
          <tr>
            <td colSpan="8">No h endereo cadastrado</td>
          </tr>
        )}
      </tbody>
    </table>
  );
}

export default CartTableAddress;
